.App {
  display: flex;
  justify-content: center;
  flex-direction: column;
  background: #3e0b13;  /* fallback for old browsers */
}

.form-container {
  background: #fff;
  border-radius: 20px;
  padding: 15px 30px;
  max-width: 800px;
  margin: 15px auto;
  box-shadow: 0 15px 16px .17px rgba(0,0,0,0.05);
}

.form-container h1 {
  margin-top: 10px;
  margin-bottom: 30px;
}

h1 {
  text-align: center;
}

#wood-image {
  width: 100%;
  height: 300px;
}

@media (min-width: 500px) {
  #wood-image {
    width: 400px;
    height: 300px;
  }
}

.nav-header {
  background: white;
  padding-left: 20px;
  padding-right: 20px;
}

.logo {
  width: 45px;
  height: 50px;
}

.check-element label {
  margin-right: 10px !important;
}

.btn-continue {
  text-align: end;
}

.btn-newRegister {
  text-align: center;
}

.form-group {
  margin-bottom: 15px;
}

.form-group > label {
  padding-bottom: 5px;
  font-weight: 600;
}

.form-group > span {
  font-style: italic;
  font-size: 14px;
}

.jumbotron {
  padding: 2rem 1rem;
  margin-bottom: 2rem;
  background-color: #e9ecef;
  border-radius: .3rem;
}

.jumbotron-info {
  background: #2cf1fc;
}

.jumbotron-success {
  background: #a6ff94;
}

.jumbotron-warn {
  background: #fffba5;
}

.center {
  text-align: center;
}

.icon-section {
  margin-bottom: 10px;
}

.btn-section {
  margin-bottom: 10px;
}